import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Data } from '../module/data';
import { Message } from '../module/message';
import { DataForm } from '../module/dataForm';
import { Observable, of } from 'rxjs';

import { random } from 'mathjs';

@Injectable({
  providedIn: 'root'
})
export class StatisticsService {
  // Place holder data used for testing purposes only
  public placeHolder : Data = {
    testName: 'hello world test',
    infrastructure: 'function',
    location: 'us-central',
    mean: 10,
    median: 8.5, 
    max: 25,
    min: 7, 
    std: 6.4,
    lst: [10,14,15,18,8,1,2,3,4,7]
  };
  // For testing
  // public testLst : Data[] = [this.placeHolder];
  public testLst : Data[] = [];

  // appends data to the list
  add(data: DataForm): void{
    let qString = `api/${data.infrastructure}/${data.location}/${data.testName}`;
    this.http.get<Data>(qString).subscribe( (res:Data) => {
      this.testLst.push(res);
    })
  }

  // returns observable containing testLst
  getLst(): Observable<Data[]>{
    return of(this.testLst);
  }



  // temporary testing data generator
  createData(data: DataForm): Data {
    return {
      testName: data.testName,
      infrastructure: data.infrastructure,
      location: data.location,
      mean: random(10, 90),
      median: random(10, 90),
      max: random(10, 90), 
      min: random(10, 90), 
      std: random(10, 90),
      lst: random([1,10], 10, 90)[0]


    }
  }

  // delete exsisting tests
  delete(): void {
    this.testLst.length = 0;
  }

  // test get
  testGet() {
    return this.http.get<Message>('api/test/angular');
  }


  
  constructor(
    private http: HttpClient,
  ) { }
}
