import { Injectable } from '@angular/core';
import { ApiStat } from '../module/apiStat';
import { ApiData } from '../module/apiData';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiTestService {
  public placeholder : ApiStat = {
    testName: "first api test",
    apiType: "REST",
    mean: 10,
    median: 8.5, 
    max: 25,
    min: 7, 
    std: 6.4,
    lst: [10,14,15,18,8,1,2,3,4,7]
  } 
  public lst : ApiStat[] = [];

    // appends data to the list
    add(data: ApiData): void{

      // let qString = `api/${data.infrastructure}/${data.location}/${data.testName}`;
      // this.http.get<Data>(qString).subscribe( (res:Data) => {
      //   this.testLst.push(res);
      // })
      
      // this.lst.push(this.placeholder)
      let qString = `api/${data.testName}/${data.apiType}`;
      this.http.get<ApiStat>(qString).subscribe(
        (res: ApiStat) => {
          this.lst.push(res)
        }
      )
      
    }

      // returns observable containing testLst
  getLst(): Observable<ApiStat[]>{
    return of(this.lst);
  }


  // delete exsisting tests
  delete(): void {
    this.lst.length = 0;
  }

  // test get
  testGet() {
    // return this.http.get<Message>('api/test/angular');
  }

  constructor(
    private http: HttpClient,
  ) { }
}
