import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { DataForm } from '../../module/dataForm';
import { StatisticsService } from '../statistics.service';


@Component({
  selector: 'app-test-form',
  templateUrl: './test-form.component.html',
  styleUrls: ['./test-form.component.css']
})
export class TestFormComponent implements OnInit {

  public sendTestForm;
  public error: string = "";


  onSubmit(data: DataForm){
    if( !data.testName || data.testName.length === 0){
      this.error = "Your test name cannot be empty!";
    } else if( !data.infrastructure || data.infrastructure.length === 0){
      this.error = "You must select an infrastructure type!";
    }else if( !data.location || data.location.length === 0){
      this.error = "You must select a location!";
    }else{
      this.error = "";
      this.statisticsService.add(data);
      this.sendTestForm.reset();
    }
  }



  constructor(
    private formBuilder: FormBuilder,
    private statisticsService: StatisticsService,
  ) { 
    this.sendTestForm = this.formBuilder.group({
      testName: '',
      infrastructure: '',
      location: '',
    });
  }

  ngOnInit(): void {
  }



}
