import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ApiData } from '../../module/apiData';
import { ApiTestService } from '../api-test.service';

@Component({
  selector: 'app-api-test-form',
  templateUrl: './api-test-form.component.html',
  styleUrls: ['./api-test-form.component.css']
})
export class ApiTestFormComponent implements OnInit {
  public apiTestForm: any;
  public error: string = "";

  constructor(
    private formBuilder: FormBuilder,
    private apiTestService: ApiTestService,
  ) { 
    this.apiTestForm = this.formBuilder.group({
      testName: '',
      apiType: '',

    })
  }

  onSubmit(data: ApiData){
    
    if( !data.testName || data.testName.length === 0){
      this.error = "Your test name cannot be empty!";
    } else if( !data.apiType || data.apiType.length === 0){
      this.error = "You must select an API type!";
    }else{
      this.error = "";
      this.apiTestService.add(data);
      this.apiTestForm.reset();
    }
  }

  ngOnInit() {
  }

}
